/**
 * Data modals and links (using central createModal)
 *
 * @version Final? Where did the security part go? Optimize in V2 update.
 */

/**
 * Core initialization and global event binding
 * @namespace Initialization
 */
document.addEventListener('DOMContentLoaded', () => {
    document.addEventListener('click', handleAllEvents);
    document.addEventListener('keydown', handleAllEvents);
});

/**
 * Central event dispatcher for click and keydown events
 * @memberof Initialization
 * @param {Event} event - The triggered event
 */
function handleAllEvents(event) {
    if (event.type === 'keydown' && event.key !== 'Enter') return;

    const target = event.target.closest('[data-link], [data-modal-link]');
    if (!target) return;

    if (target.hasAttribute('data-modal-link')) {
        event.preventDefault();
        showHtmlModal(target);
    } else if (target.hasAttribute('data-link')) {
        handleDataLink(target, event);
    }
}

/**
 * Link processing subsystem
 * @namespace LinkProcessing
 */

/**
 * Handles data-link attribute processing.
 * Decodes the base64 URL and either navigates directly or opens a confirmation modal.
 * @memberof LinkProcessing
 * @param {HTMLElement} element - The clicked element
 * @param {Event} event - The original event object
 */
function handleDataLink(element, event) {
    try {
        const dataUrl = atob(element.getAttribute('data-link'));
        if (!isValidUrl(dataUrl)) throw new Error('Invalid URL format');

        if (element.hasAttribute('data-blank')) {
            event.preventDefault();
            createModal({
                title: element.getAttribute("data-modal-title") || "Open Link",
                body: `<p>Link wird im neuen Fenster geöffnet?</p><p><a href="${dataUrl}" target="_blank">${dataUrl}</a></p>`,
                footer: { cancel: "Abbrechen", confirm: "OK" },
                onCancel: () => { },
                onConfirm: () => window.open(dataUrl, "_blank"),
            });
        } else {
            // Direct navigation if not using blank attribute
            window.location.href = dataUrl;
        }
    } catch (error) {
        console.error('Link handling failed:', error);
        alert('Error: Unable to open this link');
    }
}

/**
 * URL validation utilities
 * @namespace UrlValidation
 */

/**
 * Validates URL formats to ensure safety.
 * @memberof UrlValidation
 * @param {string} url - The URL to validate
 * @returns {boolean} True if URL is considered safe
 */
function isValidUrl(url) {
    const safePattern = /^(#|\/|\.\/|\.\.\/|[a-zA-Z0-9-]+:)/;
    try {
        if (safePattern.test(url)) return true;
        new URL(url);
        return true;
    } catch {
        return false;
    }
}

/**
 * Modal management subsystem
 * @namespace ModalSystem
 */

/**
 * Loads and displays an HTML modal based on data-modal-link attribute.
 * Fetches the corresponding HTML file and shows it using central createModal.
 * @memberof ModalSystem
 * @param {HTMLElement} element - The triggering element
 */
async function showHtmlModal(element) {




    const baseName = element.getAttribute('data-modal-link');
    const fileName = `${baseName}.html`;
    const modalTitle = element.getAttribute('data-modal-title') || 'Information';

    // console.log('Raw data-modal-link value:', baseName); // Debug-Log
    // console.log('Constructed filename:', fileName); // Debug-Log
    // const fullUrl = `/wp-content/themes/bsawesome/html/${fileName}`;
    // console.log('Full URL being fetched:', fullUrl); // Debug-Log

    // Prevent directory traversal by validating filename
    // Ersetze die alte Validierung durch diese:
    if (!/^[a-z0-9\-_\/]+$/i.test(baseName)) {
        console.error('Invalid path:', baseName);
        createModal({
            title: 'Error',
            body: 'Ungültiger Pfadformat'
        });
        return;
    }

    const modalId = `modal-${Math.random().toString(36).substring(2, 9)}`;

    try {
        const response = await fetchWithTimeout(
            `/wp-content/themes/bsawesome/html/${fileName}`,
            { method: 'GET', headers: { 'Content-Type': 'text/html' }, credentials: 'same-origin' },
            5000
        );
        if (!response.ok) throw new Error(`HTTP ${response.status}`);
        const content = await response.text();

        createModal({
            id: modalId,
            title: modalTitle,
            body: content,
            footer: { dismiss: 'Schließen' },
        });
    } catch (error) {
        console.error('Modal load failed:', error);
        createModal({
            id: modalId,
            title: modalTitle,
            body: `<div class="p-3"><p>Error loading <strong>${fileName}</strong>:</p><pre>${error.message}</pre></div>`,
            footer: { dismiss: 'Schließen' },
        });
    }


}

/**
 * Network utilities
 * @namespace Network
 */

/**
 * Performs a fetch request with a timeout mechanism.
 * @memberof Network
 * @param {string} url - URL to fetch
 * @param {Object} options - Fetch options
 * @param {number} timeout - Timeout in milliseconds
 * @returns {Promise<Response>} Fetch promise that rejects on timeout
 */
function fetchWithTimeout(url, options = {}, timeout = 8000) {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), timeout);

    return Promise.race([
        fetch(url, { ...options, signal: controller.signal })
            .finally(() => clearTimeout(timeoutId)),
        new Promise((_, reject) =>
            setTimeout(() => reject(new Error('Request timeout')), timeout)
        )
    ]);
}
