/**
 * Initializes Bootstrap tooltip that have the attribute: [data-bs-tooltip="true"]
 * 
 * @version Final
 */

function initTooltips() {
    if (!window.bootstrap?.Tooltip) return;

    // Alle Elemente mit Tooltip-Attribut suchen
    const triggers = Array.from(document.querySelectorAll('[data-bs-tooltip="true"]'));

    // Für jedes Element: Tooltip-Instanz erzeugen und ggf. Klick-Handler anhängen
    const tooltips = triggers.map(el => {
        const tooltip = new bootstrap.Tooltip(el, {
            container: 'body'   // sorgt dafür, dass Tooltip im <body> hängt
        });

        // Falls ein data-modal-* Attribut existiert, beim Klick verbergen
        if ([...el.attributes].some(attr => attr.name.startsWith('data-modal'))) {
            el.addEventListener('click', () => {
                tooltip.hide();
            });
        }

        return tooltip;
    });

    // Optional: alle Tooltips beim Öffnen irgendeines Modals verstecken
    // document.addEventListener('show.bs.modal', () => {
    //     tooltips.forEach(t => t.hide());
    // });
}

document.addEventListener('DOMContentLoaded', initTooltips);
