/**
 * Function for creating a Bootstrap modal
 *
 * @version Final
 */
function createModal(options = {}) {

    // Acquire global Bootstrap Modal constructor
    const ModalConstructor = window.bootstrap?.Modal;
    if (!ModalConstructor) {
        throw new Error('Bootstrap Modal is not available on window.bootstrap.Modal');
    }

    // Destructure options with defaults
    const {
        id = `modal-${Date.now()}`,
        title = 'Modal Title',
        body = '<p>Modal content</p>',
        // default footer dismiss button text set to German "Schließen"
        footer = { dismiss: 'Schließen' },
        size = 'lg',
        classes = '',
        onDismiss = null,
        onCancel = null,
        onConfirm = null,
        backdrop = true
    } = options;

    // Build modal markup
    const modal = document.createElement('div');
    modal.id = id;
    modal.className = 'modal fade';
    modal.tabIndex = -1;
    modal.setAttribute('role', 'dialog');
    modal.setAttribute('aria-labelledby', `${id}-label`);
    modal.innerHTML = `
      <div class="modal-dialog modal-dialog-scrollable modal-dialog-centered modal-${size} ${classes}" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="${id}-label">${title}</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">${body}</div>
          <div class="modal-footer" id="${id}-footer"></div>
        </div>
      </div>
    `;
    document.body.append(modal);

    // ARIA-hidden safeguard (remove if accidentally set)
    const observer = new MutationObserver(records => {
        records.forEach(record => {
            if (record.attributeName === 'aria-hidden' && modal.hasAttribute('aria-hidden')) {
                modal.removeAttribute('aria-hidden');
            }
        });
    });
    // Observe only aria-hidden attribute to minimize overhead
    observer.observe(modal, { attributes: true, attributeFilter: ['aria-hidden'] });

    // Populate footer buttons
    const footerEl = modal.querySelector(`#${id}-footer`);
    const actions = [
        { key: 'dismiss', class: 'btn-dark', onClick: onDismiss, dismiss: true },
        { key: 'cancel', class: 'btn-dark', onClick: onCancel, dismiss: true },
        { key: 'confirm', class: 'btn-primary', onClick: onConfirm, dismiss: false }
    ];
    actions.forEach(({ key, class: cls, onClick, dismiss }) => {
        const text = footer[key];
        if (!text) return;
        const btn = document.createElement('button');
        btn.type = 'button';
        btn.className = `btn ${cls}`;
        btn.textContent = text;
        if (dismiss) btn.dataset.bsDismiss = 'modal';
        if (typeof onClick === 'function') btn.addEventListener('click', onClick);
        footerEl.append(btn);
    });

    // Initialize and show modal
    const bsModal = new ModalConstructor(modal, { focus: true, keyboard: true, backdrop });

    // Event listener to close tooltips when modal is shown
    modal.addEventListener('shown.bs.modal', () => {
        const Tooltip = window.bootstrap?.Tooltip;
        if (Tooltip) {
            const tooltipTriggers = document.querySelectorAll('[data-*="tooltip"]');
            tooltipTriggers.forEach(tooltipTriggerEl => {
                const tooltipInstance = Tooltip.getInstance(tooltipTriggerEl);
                if (tooltipInstance) {
                    tooltipInstance.hide();
                }
            });
        }
    });

    modal.addEventListener('hidden.bs.modal', () => {
        observer.disconnect();
        modal.remove();
    });

    bsModal.show();
    return modal;
}

// Expose globally
if (typeof window !== 'undefined') {
    window.createModal = createModal;
}
